  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: local(""), url("../fonts/robotoregular.woff2") format("woff2"), url("../fonts/robotoregular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: local(""), url("../fonts/robotomedium.woff2") format("woff2"), url("../fonts/robotomedium.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: bold;
  font-display: swap;
  font-weight: 700;
  src: local(""), url("../fonts/robotobold.woff2") format("woff2"), url("../fonts/robotobold.woff") format("woff"); }

html {
  box-sizing: inherit; }

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

body {
  background-color: #ffffff;
  width: 1440px;
  margin: 0 auto;
  color: #000000; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

a {
  text-decoration: none; }

ul {
  list-style: none; }

.benefits__item {
  display: none;
  background-repeat: no-repeat;
  width: 100%;
  height: auto;
  padding: 78px 0 80px 0; }
  .benefits__item:nth-of-type(1) {
    background-image: url("../img/slide1.png");
    background-position: 93% 18px; }
    @media (min-resolution: 2dppx) {
      .benefits__item:nth-of-type(1) {
        background-image: url("../img/slide1@2x.png"); } }
  .benefits__item:nth-of-type(2) {
    background-image: url("../img/slide2.png");
    background-position: 100% 0; }
    @media (min-resolution: 2dppx) {
      .benefits__item:nth-of-type(2) {
        background-image: url("../img/slide2@2x.png"); } }
  .benefits__item:nth-of-type(3) {
    background-image: url("../img/slide3.png");
    background-position: 100% 20px; }
    @media (min-resolution: 2dppx) {
      .benefits__item:nth-of-type(3) {
        background-image: url("../img/slide3@2x.png"); } }

.benefits__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 55px;
  line-height: 55px;
  font-weight: 500;
  max-width: 600px;
  margin-bottom: 27px; }

.benefits__text {
  max-width: 410px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  margin-bottom: 40px; }

.benefits__link {
  display: block;
  width: 240px;
  padding: 14px 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  background-color: #fb565a;
  border-radius: 3px;
  border: none;
  transition: 0.4s ease; }

.benefits__link:hover,
.benefits__link:focus {
  background-color: #e74246; }

.benefits__link:active {
  color: rgba(0, 0, 0, 0.3);
  background-color: #d7373b;
  box-shadow: 0 3px rgba(0, 0, 0, 0.1) inset; }

.controls__input--1:checked ~ .benefits .benefits__item:nth-child(1) {
  display: block; }

.controls__input--2:checked ~ .benefits .benefits__item:nth-child(2) {
  display: block; }

.controls__input--3:checked ~ .benefits .benefits__item:nth-child(3) {
  display: block; }

.services__img img {
  width: 300px;
  height: 146px; }

.map {
  width: 100%;
  font-size: 0;
  background-color: grey;
  background-image: url("../img/map.png");
  background-size: 1440px 416px;
  background-repeat: no-repeat;
  display: block;
  position: relative; }
  @media (min-resolution: 2dppx) {
    .map {
      background-image: url("../img/map@2x.png");
      background-size: 2880px auto; } }

.map::before {
  content: "";
  position: absolute;
  display: block;
  width: 231px;
  height: 190px;
  background-image: url("../img/map-marker.png");
  background-repeat: no-repeat;
  z-index: 5;
  background-size: cover;
  top: 10px;
  left: 46.4%; }
  @media (min-resolution: 2dppx) {
    .map::before {
      background-image: url("../img/map-marker@2x.png");
      background-size: 462px auto; } }

.map iframe {
  width: 100%;
  height: 400px; }

.site-example__img {
  width: 360px;
  height: 576px;
  padding-top: 40px;
  position: relative; }
  .site-example__img::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 360px;
    height: 40px;
    background-image: url("../img/browser.svg");
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    opacity: 0.12; }

.page-header {
  background-color: #eeeeee; }

.page-header__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 1200px;
  display: flex;
  align-items: center;
  padding-top: 49px; }

.page-header__logo {
  margin-right: auto; }

.main-nav {
  display: flex;
  align-items: center;
  width: 800px;
  margin-top: 16px; }

.main-nav__list:first-of-type {
  margin-right: auto; }

.site-list {
  display: flex;
  width: 440px; }

.site-list__item {
  margin-right: 47px; }
  .site-list__item:last-of-type {
    margin-right: 0; }

.site-list__link {
  transition: 0.4s ease;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 30px;
  font-weight: 500;
  color: #000000;
  text-transform: uppercase;
  outline: none; }
  .site-list__link[href]:hover, .site-list__link[href]:focus {
    color: red; }
  .site-list__link[href]:active {
    color: #000000;
    opacity: 0.3; }
  .site-list__link--active {
    border-bottom: 2px solid #fb565a;
    padding-bottom: 9px; }

.logo__img {
  width: 160px;
  height: 65px;
  transition: 0.4s ease; }

.logo__link[href]:hover .logo__img,
.logo__link[href]:focus .logo__img {
  opacity: 0.8; }

.logo__link[href]:active .logo__img {
  opacity: 0.3; }

.user-list__item {
  position: relative; }

.user-list__link {
  transition: 0.4s ease;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 30px;
  font-weight: 500;
  color: #000000;
  text-transform: uppercase;
  outline: none; }
  .user-list__link[href]:hover, .user-list__link[href]:focus {
    color: red; }
  .user-list__link[href]:active {
    color: #000000;
    opacity: 0.3; }
  .user-list__link--active {
    border-bottom: 2px solid #fb565a;
    padding-bottom: 9px; }
  .user-list__link--basket {
    padding-left: 39px; }

.user-list__icon {
  position: absolute;
  top: 7px;
  left: 0;
  fill: #a6a6a6;
  transition: 0.4s ease; }

.user-list__link:hover ~ .user-list__icon,
.user-list__link:focus ~ .user-list__icon {
  fill: #fb565a; }

.user-list__link:active ~ .user-list__icon {
  fill: #000000;
  fill-opacity: 0.2; }

.slider__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 1200px;
  position: relative; }

.controls {
  position: absolute;
  left: 50%;
  bottom: 104px;
  z-index: 100;
  width: 160px;
  height: 18px;
  text-align: center;
  font-size: 0;
  transform: translateX(-50%); }

.controls__label {
  position: relative;
  display: inline-block;
  width: 18px;
  height: 18px;
  padding: 8px;
  border-radius: 50%;
  cursor: pointer;
  margin: 0 9px; }

.controls__label::after {
  content: "";
  position: absolute;
  top: 10px;
  left: -2px;
  z-index: 1;
  width: 18px;
  height: 18px;
  background: #ffffff;
  border-radius: 50%; }

.controls__input--1:checked ~ .controls .controls__label--1::before,
.controls__input--2:checked ~ .controls .controls__label--2::before,
.controls__input--3:checked ~ .controls .controls__label--3::before {
  content: "";
  position: absolute;
  left: 3px;
  top: 15px;
  z-index: 2;
  width: 8px;
  height: 8px;
  background-color: #ffffff;
  border: 2px solid #c1c1c1;
  border-radius: 50%; }

.page-heading__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 55px;
  line-height: 55px;
  font-weight: 500;
  text-align: center;
  margin: 0; }

.page-heading__wrapper {
  margin: 0 auto;
  padding: 0 20px;
  width: 1200px;
  padding-top: 79px;
  padding-bottom: 106px; }

.page-main__heading {
  background-color: #eeeeee; }

.page-main__slider {
  background-color: #eeeeee; }
